<template>
  <div class="text-center">
        <v-menu
          open-on-hover
          left
          offset-y

        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              color="accent"
              dark
              v-bind="attrs"
              v-on="on"
              icon
              text

            >
            <v-icon
              size="30px"
            >
            {{ icons.mdiFish }}
          </v-icon>
          </v-btn>
          </template>

          <v-list>
            <v-list-item
              v-for="(item, index) in items"
              :key="index"
            >
            <v-list-item-action>
            <v-icon
              color="third"
            >
            {{ item.icon }}
          </v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>
              {{ item.text }}
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
          </v-list>
        </v-menu>
      </div>
</template>

<script>
import { mdiFish, mdiLogoutVariant } from '@mdi/js';
export default{
  name: 'Avatar',
  data: () => ({
    icons: {
      mdiFish,
      mdiLogoutVariant
    },
    items: [
      {text: 'My Profile', icon:'mdi-account-circle'},
      {text: 'My Fish', icon:'mdi-fishbowl'},
      {text: 'Settings', icon:'mdi-cog'},
      {text: 'Sign Out', icon: mdiLogoutVariant}
    ]
  }),

}
</script>
